html, 
body {  
  background: #f7f7f7;
  margin: 0;    
  height: 100%;  
}

a {
  color: $color-link;
  text-decoration: none;
  &:hover {
    text-decoration: none;
    color: $color-link-hover;
    transition: color 0.2s;
  }
}

p {  
  width: 100%;
  margin: 0.5em 0;
  &:first-of-type {
    margin-top: 0;
  }
}